    width: 50%;
  }
  
  .right-container {
    border-left: none;
  }
  
  .left-container {
    background: #E71D23;
    height: 100%;
    width: calc(50% + 50px);
  }
  
  .left-container__top-section {
    border-bottom: 3px solid black;
    display: flex;
    height: 50px;
    padding: 8px;
  }
  
  .top-section__blue {
    background: radial-gradient(farthest-corner at 10px 10px, white, #369CD4, #369CD4);
    border-radius: 50%;
    border: 2px solid white;
    box-shadow: 0 0 2px 2px rgba(0,0,0,.5);
    height: 30px;
    width: 30px;
  }
  
  .top-section__small-buttons {
    display: flex;
    margin: 0 25px;
  }
  
  .top-section__red,
  .top-section__yellow,
  .top-section__green {
    border-radius: 50%;
    border: 1px solid black;
    height: 10px;
    margin-right: 5px;
    width: 10px;
  }
  
  .left-container__main-section-container {
    display: flex;
    height: calc(100% - 50px);
  }
  
  .left-container__main-section {
    height: 100%;
    padding: 25px;
    width: 500px;
  }
  
  .main-section__white {
    background: #DEDEDE;
    border: 3px solid black;
    box-shadow: inset 0 0 3px 3px rgba(0,0,0,.3);
    height: 325px;
  }
  
  .main-section__black {
    background: black;
    height: calc(100% - 50px);
    margin: 25px;
    padding: 10px;
    width: calc(100% - 50px);
  }
  
  .main-screen {
    border-radius: 15px;
    height: 100%;
    padding: 15px;
  }
  
  .screen__header {
    align-items: center;
    display: flex;
    justify-content: space-between;
  }
  
  .poke-name {
    color: white;
    font-size: 25px;
    font-weight: bold;
  }
  
  .poke-id {
    color: rgba(0,0,0,.5);
    font-size: 20px;
  }
  
  .screen__image {
    display: flex;
    justify-content: space-around;
  }
  
  .poke-front-image,
  .poke-back-image {
    height: 96px;
    width: 96px;
  }
  
  .screen__description {
    display: flex;
    height: 100px;
    justify-content: space-around;
  }
  
  .screen__stats {
    background: rgba(0,0,0,.3);
    border-radius: 5px;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    padding: 20px 15px;
  }
  
  .stats__types {
    height: 100%;
    padding: 5px;
  }
  
  .poke-type-one,
  .poke-type-two {
    background: rgba(255,255,255,.3);
    border-radius: 25px;
    display: block;
    margin-bottom: 10px;
    padding: 10px;
    text-align: center;
  }
  
  .left-container__controllers {
    align-items: center;
    display: flex;
    justify-content: space-around;
    margin: 10px 25px 0;
  }
  
  .controllers__d-pad {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
  
  .d-pad__cell {
    background: black;
    box-shadow: 0 0 2px 2px rgba(0,0,0, .3);
    height: 25px;
    width: 25px;
  }
  
  .d-pad__cell.top {
    border-left: 1px solid dimgrey;
    border-radius: 5px 5px 0 0;
    border-right: 1px solid dimgrey;
    border-top: 1px solid dimgrey;
    grid-column: 2 / span 1;
    grid-row: 1 / span 1;
  }
  
  .d-pad__cell.left {
    border-bottom: 1px solid dimgrey;
    border-left: 1px solid dimgrey;
    border-radius: 5px 0 0 5px;
    border-top: 1px solid dimgrey;
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
  }
  
  .d-pad__cell.middle {
    grid-column: 2 / span 1;
    grid-row: 2 / span 1;
  }
  
  .d-pad__cell.right {
    border-bottom: 1px solid dimgrey;
    border-radius: 0 5px 5px 0;
    border-right: 1px solid dimgrey;
    border-top: 1px solid dimgrey;
    grid-column: 3 / span 1;
    grid-row: 2 / span 1;
  }
  
  .d-pad__cell.bottom {
    border-bottom: 1px solid dimgrey;
    border-left: 1px solid dimgrey;
    border-radius: 0 0 5px 5px;
    border-right: 1px solid dimgrey;
    grid-column: 2 / span 1;
    grid-row: 3 / span 1;
  }
  
  .controllers__buttons {
    display: flex;
    justify-content: space-around;
    width: 100px;
  }
  
  .buttons__button {
    align-items: center;
    background: black;
    border-radius: 50%;
    border: 1px solid dimgrey;
    box-shadow: 0 0 2px 2px rgba(0,0,0, .3);
    color: rgba(255,255,255,.75);
    display: flex;
    height: 35px;
    justify-content: center;
    width: 35px;
  }
  
  .buttons__button:first-child {
    margin-top: 5px;
  }
  
  .buttons__button:last-child {
    margin-top: -5px;
  }
  
  
  .left-container__right {
    border-left: 3px solid black;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    width: 50px;
  }
  
  .left-container__hinge {
